import {Component,OnInit} from '@angular/core';
import { Router,RouteSegment,CanDeactivate,RouteTree } from '@angular/router';


import {Hero} from './hero';
import { HeroService } from '../service/hero.service';
import { DialogService } from '../../app-service/dialog.service';
@Component({
    selector:'my-hero-detail',
    templateUrl:"app/hero-portal/hero/hero-detail.component.html",
    styleUrls:["app/hero-portal/hero/hero-detail.component.css"],
    providers:[HeroService,DialogService]
})

export class HeroDetailComponent implements OnInit,CanDeactivate{
    hero:Hero;
    unchangeStr:string;
    constructor(private _hService:HeroService,private _dService:DialogService,
                private _rSegment:RouteSegment,
                private _r:Router){
    }
    ngOnInit(){
        let id=+this._rSegment.getParam('id');
        this._hService.getHero(id)
                      .then((hero)=>{this.hero=hero;
                                     this.unchangeStr=hero.name;
                                    });
    }
    goBack(){
        this._r.navigate(['/hero-portal/hero']);
    }
    reset(){
        this.hero.name=this.unchangeStr;
    }
    routerCanDeactivate(next:RouteTree,prev:RouteTree):any{
        if (!this.hero || this.hero.name==this.unchangeStr) {
            return true;   
        }
        return this._dService.confirm("Will you leave?");
    }
}